.reset-password-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  .change-password-form {
    margin-top: 20px;
    font-size: 12px;
    background: white;
    padding: 30px;
    box-shadow: 0px 0px 5px #efefef;
    max-width: 450px;
    width: 100%;
    .form-control-custom {
      padding-bottom: 10px;
    }
    .number-container {
      display: flex;
      align-items: center;
      input {
        text-align: center;
      }
      .plus-symbol {
        padding: 12px;
        color: #666;
      }
    }
    .button-container {
      float: right;
      padding-top: 10px;
      button {
        width: 200px;
      }
    }
  }
}
